#ptc-trident_content-protection div.inside header.ptc-trident-protection-inheritance {
  color: #E12D39;
  font-style: italic;
}

#ptc-trident_content-protection div.inside header.ptc-trident-protection-inheritance button.ptc-trident-inheritance-toggle {
  background: #627D98;
  border-radius: 3px;
  border: 1px solid #486581;
  text-shadow: 1px 2px 2px #486581;
  text-decoration: none;
  color: #FFF;
  font-size: 0.9em;
  padding: 0.5em 0.75em;
  font-weight: 400;
  cursor: pointer;
}

#ptc-trident_content-protection div.inside fieldset {
  border-top: 1px solid #627D98;
  padding: 0 0 2em;
}

#ptc-trident_content-protection div.inside fieldset legend {
  margin: 0 0 0.5em;
  font-weight: 600;
  background: #627D98;
  color: white;
  border-radius: 3px;
  padding: 0.3em 0.5em;

  background: #627D98;
  border-radius: 3px;
  border: 1px solid #486581;
  text-shadow: 1px 2px 2px #486581;
  text-decoration: none;
  color: #FFF;
  font-size: 0.9em;
  padding: 0.3em 0.75em 0.25em;
  font-weight: 400;
}

#ptc-trident_content-protection div.inside select {
  min-height: auto;
  line-height: 1.75;
}

#ptc-trident_content-protection div.inside fieldset[disabled=disabled] input {
  background: #E4E7EB;
}

#ptc-trident_content-protection div.inside fieldset[disabled=disabled] label {
  color: #9AA5B1;
  font-weight: 300;
}